import React from "react"
import Layout from "globalOrganisms/Layout"
import styled from "styled-components"
import Seo from "globalAtoms/Seo"
import getPageData from "globalLibs/helpers/get-page-data"

const PageStyle = styled.div``

export default function NotFoundPage(props) {
  const pageData = getPageData(props)

  return (
    <>
      <Seo pageData={pageData} />
      <Layout>
        <PageStyle
          as="article"
          className="container-fluid mt-48"
          style={{ maxWidth: "992px" }}
        >
          <h1 className="mb-16">Page not found</h1>
          <hr className="mt-0"></hr>
        </PageStyle>
      </Layout>
    </>
  )
}
